	export function p(a: string[]): IDICT<number>
	{
		let data: IDICT<number> = arr_cjk(a)
			.sort(function (a, b)
			{
				let len = UString.size(a);

				let r = len - UString.size(b);

				return r;
			})
			.reduce(function (data, v)
			{
				data[v] = v.length;

				return data;
			}, {})
		;

		return data;
	}

